@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-touch-callout: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media ( min-width: 1168px ) {
    .partners { width: 1170px }
    .partnersItem { width: 270px }
    .h { font-size: 36px }
}
@media ( min-width: 778px ) and ( max-width: 1167px ) {
    .partners { width: 758px }
    .partnersItem { width: 230px}
    .h { font-size: 28px }
}
@media ( max-width: 767px ) {
    .partners { width: 355px }
    .partnersItem { width: 335px }
    .h { font-size: 28px }
}
.h {
    margin: 10px;
    font-weight: 500;
    line-height: 160%;
    color: #121920
}

.partners {
    margin: 70px auto;
}

.partnersItems__container {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
}

.partnersItem {
    max-height: 140px;
    min-height: 120px;
    max-width: 335px;
    min-width: 230px;
    border: solid 1px #EBEBF1;
    border-radius: 5px;
}

.partners__partnersItem {
    margin: 10px;
}

.partners__partnersItem:hover {
    box-shadow: -3px 0 40px rgba(0, 0, 0, 0.1), 0 0 10px rgba(164, 164, 164, 0.03);
    cursor: pointer;
}

.partners__logoContainer {
    position: relative;
    top: 50%;
    transform: translate(0, -50%);
    text-align: center;
}

.partners__logo {
    height: 60px;
    width: 60px;
    object-fit: none;
}
/* 1148px = 4 partnersItem with borders and margins minus outer margins*/
/* width of header__block = header width - 148px login__block */

/* DESKTOP */
@media ( min-width: 1168px ) {
    .header { width: 1148px }
    .header__block { width: 1000px }
}
/* TABLET */
@media ( min-width: 768px ) and ( max-width: 1167px ) {
    .header { width: 758px }
    .header__block { width: 590px }
}
/* DESKTOP & TABLET */
@media ( min-width: 768px ) {
    .header__block { padding: 20px 0 }
    .login__block { padding: 20px 0; border-left: solid 1px #EBEBF1 }
    .userIcon { visibility: hidden }
    /* 120px = margins + headerLogo */
    .header__searchFormContainer { position: relative; margin: 0 40px; width: calc(100% - 120px) }
    .line { visibility: hidden }
}
/* MOBILE */
@media ( max-width: 767px ) {
    .header { width: 335px }
    /* 56px = padding 20px + searchForm*/
    /* 187px = header - login__block */
    .header__block { padding: 20px 0 58px 0; width: 187px }
    .login__block { padding: 20px 0 58px 0 }
    .headerImg.userPhoto { visibility: hidden }
    .loginName { visibility: hidden }
    .header__searchFormContainer {
        position: absolute;
        top: 80px;
        width: 335px;
    }
}

.header__container {
    width: 100%;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
    overflow: hidden;
}

.header {
    margin: auto;
    display: flex;
    align-items: center;
}

.header__block {
    display: flex;
}

.headerImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #2893B7;
}

.header__searchFormContainer {
    display: flex;
}

.login__block {
    display: flex;
    justify-content: end;
    width: 148px;
}

.loginName {
    padding: 0 10px;
    align-self: center;
    font-size: 14px;
    color: #768298;
}

.userPhoto {}

.userIcon {
    position: absolute;
    padding: 8px 0;
    height: 24px;
    width: 24px;
}

.line {
    position: absolute;
    top: 80px;
    width: 100%;
    border-bottom: solid 1px #EBEBF1;
}
@media ( min-width: 768px ) {
    .searchFormContainer { border: 1px solid #EBEBF1; border-radius: 5px }
    .searchForm { margin-left: 39px }
    .searchIcon { margin: 8px 10px }
}
@media ( max-width: 767px ) {
    .searchFormContainer { border: none }
    .searchForm { margin-left: 25px }
    .searchIcon { margin: 8px 10px 8px -4px}
}

.searchFormContainer {
    display: grid;
    align-content: center;
    /* height + border = 40px */
    height: 38px;
    width: 100%;
}

.searchForm {
    height: 24px;
    /* 45px = searchForm margin-left + own padding-right */
    width: calc(100% - 45px);
    border: none;
}

.searchForm:active, :hover, :focus {
    outline: none;
}

.searchForm:-ms-input-placeholder {
    margin: 0;
    padding: 0;
    font-size: 14px;
    color: #BBC4D6
}

.searchForm::placeholder {
    margin: 0;
    padding: 0;
    font-size: 14px;
    color: #BBC4D6
}

.searchIcon {
    position: absolute;
    height: 24px;
    width: 24px;
    background: none;
    border: none;
}

.searchIcon:before {
    content: url(/test-partners/static/media/search.689e6ef3.svg);
}
